.form-fieldset {
  margin: 0;
  padding: 0;
  border: none;
  width: 100%;

  legend {
    font-size: 12px;
    color: #999;
    padding: 0 0 10px 4px;
  }

  &__row {
    display: flex;
    flex-direction: row wrap;
    margin-left: -18px;
  }

  &__item {
    padding-left: 18px;
  }
}
